import {mapState} from 'vuex'

export default {
  name: "ElReward",
  props: ['url'],
  data() {
    return {
      vReward: []
    }
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo
    })
  },
  watch: {
    $route(v) {
      this.getList()
    },
    userInfo(v) {
      return v
    }
  },
  async mounted() {
    await this.getList()
  },
  methods: {
    async getList() {
      // console.log(+this.$route.query.iUserId || this.userInfo.iUserId);
      let data = {
        "iPageNo": 0,
        "iPageNum": 10,
        "iUserId": +this.$route.query.iUserId || this.userInfo.iUserId
      }
      let res = await this.$axios.post(`/v1/emall/${this.url}`, data)
      this.vReward = res.vRewardInfor || res.vAnswer
      // console.log(this.vReward);

    }
  }
}
