import EraxNFT from "../../../components/cell/EraxNFT";
import {mapState} from "vuex";

export default {
  name: "ElNfts",
  components: {EraxNFT},
  props: ['iUserId'],
  data() {
    return {
      vNfts: [],
      iTotal: 0,
      iPageNo: 0,
      iPageNum: 10,
      dictData: {},
      USDT: {},
      isLoading: false,
      finished: false,
      isEracEmpty: false,
    }
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo,
    }),
  },
  watch: {
    userInfo(v) {
      this.iPageNo = 0
      this.vNfts = []
      this.load()
      return v
    },
    iUserId(v){
      return v
    }
  },
  async mounted() {
    await this.getDictData()
    await this.getSymbolTick()
    this.load()
  },
  methods: {
    async load() {
      this.finished = false
      this.isLoading = true
      await this.getWalletItemList();
    },
    async getWalletItemList() {
      // let data = {
      //   sWallet: this.mateMaskToken,
      //   iSort: 1,
      //   iPageNo: this.iPageNo,
      //   iPageNum: this.iPageNum,
      // }
      let res = await this.$axios.post('/v1/emall/GetUserItemList', {
        "iPageNo": this.iPageNo,
        "iPageNum": 10,
        "iUserId": this.iUserId
      })

      this.iPageNo++;
      this.isLoading = false
      this.iTotal = res.iTotal
      this.vNfts = [...this.vNfts, ...res.vItems]
      // if (this.vNfts.length >= this.iTotal) {
      if (res.vItems.length < this.iPageNum) {
        this.finished = true
      }
      // console.log(res);
    },
    async getExploreItem(idx) {
      await this.$axios.post('/v1/emall/ExploreItem', {
        iItemId: this.vNfts[idx].iItemId
      }).then(res => {
        this.$set(this.vNfts, idx, res.stItem)
      })
    },
    async getDictData() {
      let res = await this.$axios.post('/v1/emall/GetDictData', {
        iDictType: 4
      })
      let vDictData = res.vDictData
      let dictData = {}
      for (let item of vDictData) {
        dictData[item.sDictValue] = item.sDictText
      }
      this.dictData = dictData
    },
    async getSymbolTick() {
      let resTick = await this.$axios.post('v1/market/GetSymbolTick', {
        vSymbol: ["btcusdt@miniTicker", "ethusdt@miniTicker", "brdbtc@miniTicker", "nerausdt@miniTicker"]
      })

      resTick.vSymbolTick.forEach(v => {
        this.$set(this.USDT, v.s, v.c)
      })
    }
  },

}
