import {mapState} from 'vuex'
import ElReward from "./components/ElReward";
import ElNft from "./components/ElNft";
import Clipboard from "clipboard";

export default {
  name: "index",
  components: {
    ElReward,
    ElNft,
  },
  data() {
    return {
      showSign: false,
      showSignSuccess: false,
      iAmount: 0,
      active: this.$route.params.active || 0,
      isMe: true,
      inviteCode: '',
      vBalance: {},
      user: {},
      roleInfo: {}
    }
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo
    }),
  },
  watch: {
    $route(v) {
      this.handleInit()
    },
    userInfo(v) {
      return v
    }
  },
  async mounted() {
    await this.handleInit()
  },
  methods: {
    async handleInit() {
      this.isMe = (!this.$route.query.iUserId) || this.userInfo.iUserId === +this.$route.query.iUserId
      this.inviteCode = localStorage.getItem('inviteCode') || ''
      if (this.isMe) {
        await this.GetUserExten();
        this.initCopy();
        await this.getBalance();
      } else {
        await this.GetFriendExten(+this.$route.query.iUserId)
      }
    },
    async GetUserExten() {
      let res = await this.$axios.post('/v1/udc/GetUserExten', {
        iUserId: this.userInfo.iUserId
      })
      this.roleInfo = res.stUserExtenInfo
    },
    async GetFriendExten(iUserId) {
      let res = await this.$axios.post('/v1/udc/GetFriendExten', {
        iUserId
      })
      // console.log(res);

      this.roleInfo = res
    },

    async handleCheck() {
      let res = await this.$axios.post('/v1/emall/IsTodayCheckined', {})
      if (res.bCheckIned) {
        // this.showSignSuccess = true
        this.$toast(this.$t('Mine31'))
        return
      }
      this.showSign = true
    },
    initCopy() {
      let btn = document.getElementById("copy");
      let clipboard = new Clipboard(btn);

      clipboard.on("success", () => {
        this.$toast(this.$t('Mine32'));
      });

    },
    async getBalance() {
      let res = await this.$axios.post('/v1/udc/GetUserBalance', {iAcctType: 0})
      if (res.vBalance.length) {
        this.vBalance = res.vBalance[0]
        // console.log(this.vBalance);

      }
    },
    async handleSign() {
      let res = await this.$axios.post('/v1/emall/CheckIn', {})
      this.showSign = false
      this.showSignSuccess = true
      this.iAmount = res.iAmount
      this.$store.dispatch('UPDATE_USERINFO')
    },
    handlePopClose() {
      this.showSign = false
      this.showSignSuccess = false
    }
  }

}
